.stink__overlay{
  position: fixed;
  z-index: -100;
  height: 0;
  width: 0;
  background-color: #000;
  background-color: rgba(0,0,0, .7);
  top: 0;
  left: 0;
  transition: opacity .5s;
}

.stink__overlay.no{
  opacity: 0;
  visibility: hidden;
  transition: all .7s;
}

.stink__overlay.yes{
  opacity: 1;
  visibility: visible;
  z-index: 9999999999;
  height: 110vh;
  width: 100vw;
  transition: opacity .7s;
}

.stink__content{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-190%, -50%);
  opacity: 0;
  transition: all .7s;
}

.stink__overlay.yes .stink__content{
  opacity: 1;
  transform: translate(-50%, -50%);
}

.stink__bubble {
  position: absolute;
  background: #ffffff;
  border-radius: .4em;
  top: -65px;
  right: -155px;
  padding: 3rem 1rem;
  max-width: 180px;
}

.stink__bubble:after {
	content: '';
	position: absolute;
	bottom: 0;
	left: 50%;
	width: 0;
	height: 0;
	border: 20px solid transparent;
	border-top-color: #ffffff;
	border-bottom: 0;
	border-left: 0;
	margin-left: -10px;
	margin-bottom: -20px;
}

.stink__pic{
  width: 25rem;
  height: 25rem;
}

.stink__pic img{
  height: 100%;
  width: 100%;
}

.stink__button{
  position: absolute;
    height: 5rem;
    width: 5rem;
    left: 80%;
    top: 20px;
    cursor: pointer;
}

.stink__icon{
  height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.stink__icon .fas{
  color: #fff;
    font-size: 30px;
}

.emailjs-success{
  display: none;
}

.form__submitted{
  display: none;
}

.emailjs-success ~ .form__submitted{
  display: block;
}

.showing-stink{
  overflow: hidden;
  position: fixed;
  width: 100%;
}

/* .xl-modal */

@media only screen and (max-width: 767px){
  .stink__content{
    left: 40%;
  }
  .stink__button{
    left: 85%;
  }
}

@media only screen and (max-width: 600px){
  .stink__content{
    left: 30%;
  }
}

@media only screen and (max-width: 415px){
  .stink__pic {
    width: 22rem;
    height: 22rem;
  }
  .stink__bubble {
    top: -80px;
    right: -100px;
    padding: 1rem;
  }
  .stink__text{
    font-size: 14px;
  }
}

@media only screen and (max-width: 375px){
  .stink__overlay.yes .stink__content {
    transform: translate(-45%, -20%);
  }
}

@media only screen and (max-width: 325px){
  .stink__bubble{
    right: -85px;
  }
}
