.header{
  height: 100vh;
  position: relative;
}

.header__dets{
  background-color: #FDD4D9;
  height: 100%;
  font-family: 'Playfair Display', serif;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.header__hero{
  background-image: url(/images/happy-couple-1.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 100%;
}

.header__rsvp{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.header__btn{
  border: 4px solid #fff;
  background: #fff;
  background: rgba(255, 255, 255, .5);
  height: 7rem;
  width: 7rem;
  border-radius: 150%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Fauna One', serif;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  letter-spacing: 0.1em;
  cursor: pointer;
}

@media (max-width: 768px){
  .header__col{
    height: 50%;
  }
}
