.information{
  background-color: #f7f7f7;
  padding: 2rem;
}

.information__data--img-cont{
  height: 250px;
  width: 250px;
  border-radius: 100px;
  margin: 0 auto;
}

.information__data--img{
  height: 100%;
  width: 100%;
  border-radius: 100%;
}

.information__data--datawrap{
  margin: 0 auto;
}

.information__data--text{
  line-height: 39px;
  font-size: 19.5px;
}

.information__data .main--heading-sm{
  color: #393E46;
}

.data__container:nth-child(even) .information__data--datawrap{
  order: 1;
}

.data__container:nth-child(even) .information__data--imgwrap{
  order: 2;
}

.data__container:nth-child(odd) .information__data--datawrap{
  order: 2;
}

.data__container:nth-child(odd) .information__data--imgwrap{
  order: 1;
}


@media(max-width: 768px){
  .information .main--heading,
  .main--heading-sm,
  .information__data--text{
    text-align: center;
  }

  .information__data--img-cont{
    margin-bottom: 1rem;
  }

  .information__data .data__container .information__data--datawrap{
    order: 2;
  }

  .information__data .data__container .information__data--imgwrap{
    order: 1;
  }

  .information__data--text{
    font-size: 1.1rem;
  }

  .information__data--img-cont {
    height: 225px;
    width: 225px;
  }
}
