.nav{
  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
}

.nav.white-header{
  background-color: rgba(255,255,255, .7);
}

.nav__nav{
  padding: 1rem 2rem;
}

.nav__list{
  display: flex;
  list-style: none;
  padding-left: 0;
  margin: 0;
}

.nav__list--item{
  margin-right: 2rem;
  cursor: pointer;
}

.nav__list--item-link{
  font-family: 'Fauna One', serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  letter-spacing: 0.1em;
  color: #000;
  transition: all .4s ease;
}

.nav__list--item-link:hover{
  color: #fafafa;
  text-decoration: none;
}

.nav__list--item-link:after{
  content: '';
  height: 2px;
  width: 100%;
  background-color: #fafafa;
  margin-top: 2px;
  opacity: 0;
  display: block;
  transition: all .4s ease;
}

.nav__list--item-link:hover:after{
  opacity: 1;
}
