body{
  font-family: 'Playfair Display', serif;
}

.sub--heading{
  font-family: 'Fauna One', serif;
  font-size: 22px;
  color: #393E46;
  margin-bottom: .5rem;
}

.main--heading{
  font-family: 'Playfair Display', serif;
  font-style: normal;
  font-weight: normal;
  font-size: 48px;
  line-height: 55px;
  letter-spacing: 0.1em;
}


.main--heading-sm{
  font-family: 'Playfair Display', serif;
  font-style: normal;
  font-weight: normal;
  font-size: 1.75rem;
  line-height: 55px;
  letter-spacing: 0.1em;
}

.form-control{
  border: none;
}

.form-control:focus{
  box-shadow: 0 0 0 0.2rem rgb(240, 199, 204, .6);
}

@media (max-width: 768px){
  .sub--heading {
    font-size: 16px;
  }

  .main--heading {
    font-size: 34px;
  }

  .main--heading-sm{
    font-size: 1.4rem;
    line-height: 30px;
  }
}
